.page {
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  font-size: 10px;

  &__cards-container {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    max-width: 1024px;
    margin: 0 auto;
    padding: 50px 40px;

    & > :nth-child(n) {
      margin-right: 48px;
      margin-bottom: 46px;
    }
  
    & > :nth-child(4n) {
      margin-right: 0;
    }
  }
}
