.product-card {
  display: flex;
  flex-direction: column;

  padding: 32px 16px 16px;
  width: 200px;
  border: 1px solid $border-color;
  border-radius: 5px;
  background-color: $card-backround-color;

  &__img-container {
    display: flex;
    justify-content: center;
  }
  
  &__product-img {
    height: 134px;
  }
  
  &__title {
    font-weight: 500;
    font-size: 1.2rem;
    @extend %large-line-height;
    color: $main-accent-color;
  
    margin-top: 40px;
  }
  
  &__product-id {
    font-size: 10px;
    @extend %medium-line-height;
    
    color: $secondary-color;
    margin: 4px 0 16px;
  }
  
  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  &__responses-text {
    @extend %medium-line-height;
    color: $main-accent-color;
  }
  
  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin: 24px 0 16px;
  }
  
  &__price-text {
    color: $secondary-color;
    font-size: 1.2rem;
    @extend %large-line-height;
  }
  
  &__amount {
    font-size: 1.6rem;
    font-weight: 700;
    @extend %large-line-height;
  
    color: $main-accent-color;
  }
  
  &__buy-btn {
    padding: 11px 0;

    background-color: $blue-accent-color;
    border-radius: 5px;

    color: $card-backround-color;
    text-transform: uppercase;
    text-align: center;

    font-family: inherit;
    font-weight: 700;
    font-size: 1.4rem;
    line-height: 16px;

    border: 1px solid $blue-accent-color;
    text-decoration: none;
    transition: color 0.5s, background-color 0.5s;

    &:hover {
      background-color: $card-backround-color;
      color: $blue-accent-color;
    }
  }
}
