.nav-bar {
  &__list {
    display: flex;
    list-style: none;
  }
  
  &__item {
    display: flex;
    position: relative;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  
  &__link {
    display: flex;
    align-items: center;
    height: 60px;
    color: $main-accent-color;
    font-size: 1.2rem;
    font-weight: 500;
    text-transform: uppercase;
    text-decoration: none;

    &:hover {
      color: $blue-accent-color;
    }
  }
}

.is-active {
  color: $blue-accent-color;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 4px;
    background-color: $blue-accent-color;
    border-radius: 2px;
  }
}
